import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonHeader,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import React from "react";
import logo from "../assets/Logo.png";

function Header({title}) {
  return (
    <IonHeader>
      <IonToolbar color="primary">
        <IonButtons slot="start">
          <IonBackButton />
        </IonButtons>
        <img slot="start" style={{ height: "10vh" }} src={logo} />

        <IonTitle >{title}</IonTitle>
        <div slot="end" className="links">
          <IonButton color="secondary" routerLink="/Donate">
            Donate
          </IonButton>
          <IonButton color="secondary" routerLink="/Shop">
            Shop
          </IonButton>
        </div>
      </IonToolbar>
    </IonHeader>
  );
}

export default Header;
